<template>
  <v-container>
    <v-layout row my-4>
      <v-flex xs12 sm6 offset-sm2>
        <h2>Meetups</h2>
      </v-flex>
    </v-layout>
    <v-layout row class="my-3">
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
        <v-toolbar dark class="red darken-2">
        <v-toolbar-side-icon dark class="hidden-sm-and-up" @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-text-field  class="ml-2" v-model="search" label="Searching by meetup title" hide-details dark></v-text-field>
        </v-toolbar>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-for="meetup in meetups" :key="meetup.id" class="mb-2">
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
        <v-card class="info">
          <v-container fluid>
            <v-layout row>
              <v-flex xs5 sm4 md3>
                <v-card-media
                  :src="meetup.imageUrl"
                  height="130px">
                </v-card-media>
              </v-flex>
              <v-flex xs7 sm8 md9>
                <v-card-title primary-title>
                  <div>
                    <h5 class="white--text mb-0">{{ meetup.title }}</h5>
                    <div>{{ meetup.date }}</div>
                  </div>
                </v-card-title>
                <v-card-actions>
                  <v-btn flat :to="'/meetups/' + meetup.id">
                    <v-icon left light>arrow_forward</v-icon>
                    View Meetup
                  </v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
     </v-layout>
    <v-layout row mb-5>
   </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        search: '',
        drawer: true
      }
    },
    computed: {
      meetups () {
        return this.$store.getters.loadedMeetups.filter( meetup => {return meetup.title.toLowerCase().includes(this.search.toLowerCase())})
      }
    }
  }
</script>



